import React from 'react';
import { makeStyles} from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { grey } from '@material-ui/core/colors';
import Link from '@material-ui/core/Link';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import TwitterIcon from '@material-ui/icons/Twitter';
import SubscriptionsIcon from '@material-ui/icons/Subscriptions';
import Hidden from '@material-ui/core/Hidden';
import CssBaseline from '@material-ui/core/CssBaseline';

const useStyles = makeStyles ({
    root: {
        height: '14vh',
        backgroundColor: '#222',
        
    },
    icons: {
        marginRight: '32rem',
        marginLeft: '32rem',
        paddingTop: '0.7rem',
        display: 'flex',
        justifyContent: 'space-between',
    },
    icono: {
        color: 'grey',
        '&:hover': {
            color: 'white',
        },
    },
    nombre: {
        '&:hover': {
            color: 'white',
        },
    },
    text: {
        // color: 'white',
        display: 'flex',
        justifyContent: 'center',
        paddingTop: '0.7rem',
    },

    rootCel: {
        width: '100%',
        height: '6rem',
        backgroundColor: '#222',
    },
    iconsCel: {
        paddingTop: '0.7rem',
        display: 'flex',
        justifyContent: 'center',
    },
    iconoCel: {
        marginLeft: '1rem',
        marginRight: '1rem',
        color: 'grey',
        '&:hover': {
            color: 'white',
        },
    },
    nombreCel: {
        '&:hover': {
            color: 'white',
        },
    },
    textCel: {
        display: 'block',
        justifyContent: 'center',
        paddingTop: '0.3rem',
    },
})

const Footer = () => {
    const classes = useStyles();

    return (
        <div>
            {/* Pantalla adaptada a Escritorio MD y LG */}
            <Hidden only={['xs', 'sm']}>
                <section className={classes.root}>
                <div className={classes.icons}>
                <Link href=""><FacebookIcon className={classes.icono} /></Link>
                <Link href=""><InstagramIcon className={classes.icono} /></Link>
                <Link href=""><TwitterIcon className={classes.icono} /></Link>
                <Link href=""><SubscriptionsIcon className={classes.icono} /></Link>
                </div>
                <Typography variant="body2" className={classes.text} style={{ color: grey[500] }} align="center">
                    {'Copyright ©  '}
                    <Link color="inherit" href="/comida-facil/" className={classes.nombre} style={{ textDecoration: 'none' }}>
                        Comida Fácil - 
                    </Link> Developed by: Alexis Carreras{' '}
                    {new Date().getFullYear()}
                    {'.'}
                </Typography>
                </section>
            </Hidden>

            {/* Pantalla adaptada a Celular XS */}
            <Hidden only={['sm', 'md', 'lg', 'xl']}>
            <CssBaseline />
                <section className={classes.rootCel}>
                <div className={classes.iconsCel}>
                <Link href=""><FacebookIcon className={classes.iconoCel} /></Link>
                <Link href=""><InstagramIcon className={classes.iconoCel} /></Link>
                <Link href=""><TwitterIcon className={classes.iconoCel} /></Link>
                <Link href=""><SubscriptionsIcon className={classes.iconoCel} /></Link>
                </div>
                <Typography variant="body2" className={classes.textCel} style={{ color: grey[500] }} align="center">
                    {'Copyright ©  '}
                    <Link color="inherit" href="/comida-facil/" className={classes.nombreCel} style={{ textDecoration: 'none' }}>
                        Comida Fácil - <br />
                    </Link> Developed by: Alexis Carreras{' '}
                    {new Date().getFullYear()}
                    {'.'}
                </Typography>
                </section>
            </Hidden>
        </div>

    );
}

export default Footer;